"use client";
import React, { useState } from "react";
import { BiChevronDown, BiMenu } from "react-icons/bi";
import Link from "next/link";
import useAuthStore from "@/store/authStore";

export default function UserHeader() {
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const { user, logout } = useAuthStore();

  const handleToggleDropdown = () => setDropdownOpen(!dropdownOpen);

  const handleLogout = () => {
    logout();
    window.location.href = "/login";
  };

  const getInitials = (name) => {
    if (!name) return "U";
    const names = name.split(" ");
    if (names.length >= 2) {
      return (names[0][0] + names[1][0]).toUpperCase();
    }
    return name[0].toUpperCase();
  };

  return (
    <header className="fixed top-0 left-0 right-0 h-16 bg-blue-50 flex items-center justify-between px-6 shadow-sm z-40 border-b border-gray-200">
      {/* Left Side - Logo and Hamburger */}
      <div className="flex items-center gap-3">
        <span className="text-green-600 text-xl">üå±</span>
        <h1 className="text-lg font-bold text-blue-900">Care Foundation Trust</h1>
        <button className="ml-4 text-gray-600 hover:text-gray-900">
          <BiMenu className="text-2xl" />
        </button>
      </div>

      {/* Right Side - User Profile */}
      <div className="relative">
        <button
          onClick={handleToggleDropdown}
          className="flex items-center gap-3 focus:outline-none hover:bg-white/50 rounded-lg px-3 py-2 transition-colors"
        >
          <div className="w-10 h-10 bg-blue-600 rounded-full flex items-center justify-center">
            <span className="text-white font-bold text-sm">
              {user?.avatar ? (
                <img src={user.avatar} alt={user.name} className="w-full h-full rounded-full object-cover" />
              ) : (
                getInitials(user?.name || "User")
              )}
            </span>
          </div>
          <span className="text-gray-900 font-semibold text-sm hidden md:block">
            {user?.name || "User"}
          </span>
          <BiChevronDown className={`text-gray-400 transition-transform ${dropdownOpen ? 'rotate-180' : ''}`} />
        </button>

        {dropdownOpen && (
          <ul className="absolute right-0 mt-2 w-64 bg-white border border-gray-200 rounded-xl shadow-xl py-2 z-50">
            <li className="px-5 py-3 border-b border-gray-100">
              <h6 className="font-semibold text-gray-900">
                {user?.name || "User"}
              </h6>
              <span className="text-gray-500 text-xs block mt-1">
                {user?.email || "user@example.com"}
              </span>
            </li>

            <li>
              <Link
                href="/profile"
                className="flex items-center gap-3 px-5 py-3 hover:bg-blue-50 transition-colors duration-200"
                onClick={() => setDropdownOpen(false)}
              >
                <span className="text-gray-600 text-lg">üë§</span>
                <span className="text-gray-700 font-medium text-sm">
                  My Profile
                </span>
              </Link>
            </li>

            <li>
              <Link
                href="/"
                className="flex items-center gap-3 px-5 py-3 hover:bg-blue-50 transition-colors duration-200"
                onClick={() => setDropdownOpen(false)}
              >
                <span className="text-gray-600 text-lg">üè†</span>
                <span className="text-gray-700 font-medium text-sm">
                  Front Side
                </span>
              </Link>
            </li>

            <li>
              <button
                onClick={handleLogout}
                className="flex items-center gap-3 px-5 py-3 hover:bg-red-50 transition-colors duration-200 text-red-600 w-full text-left"
              >
                <span className="text-lg">üö™</span>
                <span className="font-medium text-sm">
                  Log Out
                </span>
              </button>
            </li>
          </ul>
        )}
      </div>
    </header>
  );
}

