"use client";
import React, { useState } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import {
  BiGrid,
  BiRupee,
  BiCode,
  BiUser,
  BiHome,
  BiLogOut,
  BiChevronDown,
} from "react-icons/bi";
import useAuthStore from "@/store/authStore";

const sidebarData = {
  main: [
    { title: "User Dashboard", icon: <BiGrid />, href: "/dashboard" },
  ],
  supportUs: [
    {
      title: "Support Us",
      icon: <BiRupee />,
      children: [
        { title: "My Donation History", href: "/my-donations" },
      ],
    },
  ],
  couponSection: [
    {
      title: "Coupon Section",
      icon: <BiCode />,
      children: [
        { title: "Unused Coupons", href: "/redeem-coupon" },
        { title: "Used Donated Coupons", href: "/redeem-coupon?used=true" },
      ],
    },
  ],
  profile: [
    { title: "My Profile", icon: <BiUser />, href: "/profile", type: "link" },
    { title: "Front Side", icon: <BiHome />, href: "/", type: "link" },
    { title: "Log Out", icon: <BiLogOut />, href: "#", type: "button" },
  ],
};

export default function UserSidebar() {
  const [openMenus, setOpenMenus] = useState({
    "Support Us": true,
    "Coupon Section": false,
  });
  const pathname = usePathname();
  const router = useRouter();
  const { logout } = useAuthStore();

  const handleLogout = () => {
    logout();
    router.push("/login");
  };

  const toggleMenu = (title) => {
    setOpenMenus((prev) => ({ ...prev, [title]: !prev[title] }));
  };

  const isActive = (href) => {
    if (href === "/dashboard") {
      return pathname === "/dashboard";
    }
    return pathname === href || pathname?.startsWith(href);
  };

  const renderMenuItem = (item) => {
    if (item.children) {
      const isOpen = openMenus[item.title];
      return (
        <li key={item.title}>
          <button
            className="flex items-center justify-between w-full px-4 py-3 text-left rounded-md hover:bg-gray-50 transition-colors duration-200 group"
            onClick={() => toggleMenu(item.title)}
          >
            <span className="flex items-center gap-3">
              <span className="inline-block text-gray-600 group-hover:text-green-600 text-lg">
                {item.icon}
              </span>
              <span className="text-sm font-medium text-gray-700 group-hover:text-gray-900">
                {item.title}
              </span>
            </span>
            <span
              className={`transition-transform duration-200 inline-block text-gray-400 ${
                isOpen ? "rotate-180" : ""
              }`}
            >
              <BiChevronDown />
            </span>
          </button>
          {isOpen && (
            <ul className="pl-11 mt-1 space-y-1">
              {item.children.map((child) => {
                const childActive = isActive(child.href);
                return (
                  <li key={child.title}>
                    <Link
                      href={child.href}
                      className={`flex items-center gap-2 px-3 py-2 rounded-md text-sm transition-colors duration-200 ${
                        childActive
                          ? "bg-green-50 text-green-600 font-semibold"
                          : "text-gray-600 hover:bg-gray-50 hover:text-green-600"
                      }`}
                    >
                      <span
                        className={`w-2 h-2 rounded-full ${
                          childActive ? "bg-green-600" : "bg-gray-300"
                        }`}
                      ></span>
                      {child.title}
                    </Link>
                  </li>
                );
              })}
            </ul>
          )}
        </li>
      );
    }

    const active = isActive(item.href);
    
    if (item.type === "button") {
      return (
        <li key={item.title}>
          <button
            onClick={handleLogout}
            className="flex items-center gap-3 px-4 py-3 rounded-md transition-colors duration-200 group w-full text-left text-gray-700 hover:bg-gray-50 hover:text-red-600"
          >
            <span className="inline-block text-lg text-gray-500 group-hover:text-red-600">
              {item.icon}
            </span>
            <span className="text-sm font-medium">{item.title}</span>
          </button>
        </li>
      );
    }
    
    return (
      <li key={item.title}>
        <Link
          href={item.href || "#"}
          className={`flex items-center gap-3 px-4 py-3 rounded-md transition-colors duration-200 group ${
            active
              ? "bg-green-100 text-green-700 font-semibold"
              : "text-gray-700 hover:bg-gray-50 hover:text-green-600"
          }`}
        >
          <span
            className={`inline-block text-lg ${
              active ? "text-green-600" : "text-gray-500 group-hover:text-green-600"
            }`}
          >
            {item.icon}
          </span>
          <span className="text-sm font-medium">{item.title}</span>
        </Link>
      </li>
    );
  };

  return (
    <aside className="w-64 min-h-screen bg-white border-r border-gray-200 fixed left-0 top-16 overflow-y-auto">
      <div className="py-4">
        {/* Main Dashboard */}
        <ul className="space-y-1 mb-6 px-3">
          {sidebarData.main.map((item) => renderMenuItem(item))}
        </ul>

        {/* Support Us Section */}
        <ul className="space-y-1 mb-6 px-3">
          {sidebarData.supportUs.map((item) => renderMenuItem(item))}
        </ul>

        {/* Coupon Section */}
        <ul className="space-y-1 mb-6 px-3">
          {sidebarData.couponSection.map((item) => renderMenuItem(item))}
        </ul>

        {/* Profile Section */}
        <div className="px-4 mb-3">
          <h3 className="text-xs font-bold text-gray-500 uppercase tracking-wider">
            PROFILE SECTION
          </h3>
        </div>
        <ul className="space-y-1 px-3">
          {sidebarData.profile.map((item) => renderMenuItem(item))}
        </ul>
      </div>
    </aside>
  );
}

